
body {
  overflow-x: hidden;
  background-image: url(../img/background.4a6cc231.png);
  font-family: Audiowide;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
a {
  text-decoration: none;
}
hr {
  border-top: 2px dashed  #5D354D;
}
.color-hl {
  color: #FCDA06;
}
.color-main {
  color: #5D354D;
}
.color-hl-bg {
  background-color: #FCDA06;
}
.color-main-bg {
  background-color: #5D354D;
}
#app {
  height: 100%;
  width: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
}
#error-block {
  width: 100%;
  text-align: center;
  color: red;
}
#fox {
  margin: 50px;
}
#main-container {
  margin-top: 20px;
  margin-bottom: 50px;
}
footer {
  background-color:rgba(93, 53, 77, 0.7);
}
footer,
header,
main,
aside {
  padding: 10px;
}
footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  text-align: center;
}
.c-button {
  padding-top: 0.5em !important;
  font-family: Audiowide;
}
.c-button-icon {
  color: #5D354D;
}
.c-button-icon {
  color: #5D354D;
}
.c-button-content {
  color: #5D354D;
  font-family: Audiowide;
}

/* "lg" row */
@media screen and (max-width: 993px) {
#wallet-container {
    display: none;
}
}
