
.loader {
  position: absolute;
  height: 200px;
  top: 50%;
  left: 0;
  right: 0;
  margin: -100px auto;
}
.circleFW,
.circleSW {
  width: 100%;
  height: 100%;
  transform-origin: center center;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}
.circleFW {
  -webkit-animation: rotate 2s linear infinite;
          animation: rotate 2s linear infinite;
}
.circleSW {
  -webkit-animation: rotate-reverse 4s linear infinite;
          animation: rotate-reverse 4s linear infinite;
}
.path,
.path2,
.path3,
.path4,
.path5,
.path6,
.path7,
.path8,
.path9 {
  stroke-linecap: square;
  stroke-width: 33;
  opacity: 0.8;
}
.path {
  stroke-dasharray: 1400, 2600;
  stroke-dashoffset: 0;
  -webkit-animation: dash 3.7s ease-in-out infinite;
          animation: dash 3.7s ease-in-out infinite;
  opacity: 0.3;
}
.path2 {
  stroke-dasharray: 40, 2600;
  stroke-dashoffset: -500px;
}
.path3 {
  stroke-dasharray: 200, 2600;
  stroke-dashoffset: -700px;
}
.path4 {
  stroke-dasharray: 40, 2600;
  stroke-dashoffset: -2000px;
}
.path5 {
  stroke-dasharray: 100, 1600;
  stroke-dashoffset: 0;
}
.path6 {
  stroke-dasharray: 300, 2600;
  stroke-dashoffset: -1000px;
}
.path7 {
  stroke-dasharray: 40, 2600;
  stroke-dashoffset: -1200px;
}
.path8 {
  stroke-dasharray: 100, 2600;
  stroke-dashoffset: -300px;
}
.path9 {
  stroke-dashoffset: -300px;
  opacity: 0.8;
}
@-webkit-keyframes rotate {
100% {
    transform: rotate(360deg);
}
}
@keyframes rotate {
100% {
    transform: rotate(360deg);
}
}
@-webkit-keyframes rotate-reverse {
100% {
    transform: rotate(-360deg);
}
}
@keyframes rotate-reverse {
100% {
    transform: rotate(-360deg);
}
}
@-webkit-keyframes dash {
0% {
    stroke-dasharray: 2000, 2600;
    stroke-dashoffset: 0;
}
50% {
    stroke-dasharray: 2400, 2600;
    stroke-dashoffset: -500px;
}
100% {
    stroke-dasharray: 2000, 2600;
    stroke-dashoffset: 0;
}
}
@keyframes dash {
0% {
    stroke-dasharray: 2000, 2600;
    stroke-dashoffset: 0;
}
50% {
    stroke-dasharray: 2400, 2600;
    stroke-dashoffset: -500px;
}
100% {
    stroke-dasharray: 2000, 2600;
    stroke-dashoffset: 0;
}
}
@-webkit-keyframes dash2 {
0% {
    stroke-dasharray: 100, 2600;
    stroke-dashoffset: -2000px;
}
50% {
    stroke-dasharray: 500, 2600;
    stroke-dashoffset: -1000px;
}
100% {
    stroke-dasharray: 100, 2600;
    stroke-dashoffset: -1960px;
}
}
@keyframes dash2 {
0% {
    stroke-dasharray: 100, 2600;
    stroke-dashoffset: -2000px;
}
50% {
    stroke-dasharray: 500, 2600;
    stroke-dashoffset: -1000px;
}
100% {
    stroke-dasharray: 100, 2600;
    stroke-dashoffset: -1960px;
}
}
