
.active-route {
  background-color: #22edfc !important;
}
#logo {
  width: 50px;
  height: 50px;
  overflow: hidden;
  border-radius: 50%;
  padding: 5px;
}
#logo > a {
  text-decoration: none;
}
#logo > a > img {
  width: 100%;
}
#logo-text {
  font-weight: lighter;
  filter: drop-shadow(0 0 1px #F8DA00);
}
#network {
  margin-left: 10px;
}
#network > button {
  background: none;
  cursor: default;
}
#network > button > span {
  color: greenyellow;
  font-weight: bolder;
  margin-right: 5px;
}
#network > button > .c-button-icon {
  font-size: 1.3em;
  margin-top: -0.2em;
}
#network.testnet > button > .c-button-content {
  transform: scale(1);
	-webkit-animation: pulse-red 2s infinite;
	        animation: pulse-red 2s infinite;
  box-shadow: 0 0 0 0 rgba(255, 0, 0, 1);
}
#network.mainnet > button > .c-button-content {
  transform: scale(1);
	-webkit-animation: pulse-green 2s infinite;
	        animation: pulse-green 2s infinite;
  box-shadow: 0 0 0 0 rgba(173, 255, 47, 1);
}
@-webkit-keyframes pulse-red {
0% {
		/* transform: scale(0.95); */
		box-shadow: 0 0 0 0 rgba(255, 0, 0, 0.7);
}
70% {
		/* transform: scale(1); */
		box-shadow: 0 0 0 10px rgba(255, 0, 0, 0);
}
100% {
		/* transform: scale(0.95); */
		box-shadow: 0 0 0 0 rgba(255, 0, 0, 0);
}
}
@keyframes pulse-red {
0% {
		/* transform: scale(0.95); */
		box-shadow: 0 0 0 0 rgba(255, 0, 0, 0.7);
}
70% {
		/* transform: scale(1); */
		box-shadow: 0 0 0 10px rgba(255, 0, 0, 0);
}
100% {
		/* transform: scale(0.95); */
		box-shadow: 0 0 0 0 rgba(255, 0, 0, 0);
}
}
@-webkit-keyframes pulse-green {
0% {
		/* transform: scale(0.95); */
		box-shadow: 0 0 0 0 rgba(173, 255, 47, 0.7);
}
70% {
		/* transform: scale(1); */
		box-shadow: 0 0 0 10px rgba(173, 255, 47, 0);
}
100% {
		/* transform: scale(0.95); */
		box-shadow: 0 0 0 0 rgba(173, 255, 47, 0);
}
}
@keyframes pulse-green {
0% {
		/* transform: scale(0.95); */
		box-shadow: 0 0 0 0 rgba(173, 255, 47, 0.7);
}
70% {
		/* transform: scale(1); */
		box-shadow: 0 0 0 10px rgba(173, 255, 47, 0);
}
100% {
		/* transform: scale(0.95); */
		box-shadow: 0 0 0 0 rgba(173, 255, 47, 0);
}
}
#network.testnet > button > .c-button-icon {
  color: red;
}
#network.mainnet > button > .c-button-icon {
  color: greenyellow;
}
.controls {
  justify-content: flex-end;
  display: flex;
  align-items: center;
  padding-right: 10px !important;
}
@media screen and (max-width: 333px) {
#network {
    display: none;
}
}

/* "lg" row */
@media screen and (max-width: 993px) {
#logo-container,
  #logo-text {
    display: none;
}
.controls {
    text-align: center;
    padding-right: none;
    justify-content: center;
}
}
@media screen and (max-width: 550px) {
.buttons-long-desc {
    display: none;
}
}
