
a.address-copy {
  cursor: pointer;
}
.eth-address {
  line-height: 50px;
  white-space: nowrap;
  overflow: hidden;
  cursor: pointer;
  text-align: right;
}
.eth-address:hover {
  color: #5D354D;
}
#wallet > .c-table-wrapper {
  margin-top: 15px;
  margin-bottom: 10px;
}
#wallet > .c-table-wrapper > .c-table > thead {
  display: none;
}
.etherscan-icon,
.etherscan-text {
  display: none;
}

/* "lg" row */
@media screen and (max-width: 993px) {
.etherscan-icon {
    display: inline-block;
}
#wallet-container {
    display: none;
}
}

/* "lg" row */
@media screen and (min-width: 993px) {
.etherscan-text {
    display: inline-block;
}
}
